<template lang="html">
  <div class="demo-modal-alert">
    <modal-alert :show="show1" title="标题" content="你好大好费拉达斯反对撒娇了" @click-alert="callClickAlert"></modal-alert>
    <modal-alert :show="show2" title="标题" content="你好大好费拉达斯反对撒娇了" cancel-text="取消" @click-alert="callClickConfirm"></modal-alert>
    <button type="button" class="demo-modal-alert-button" @click="showAlert">Alert</button>
    <button type="button" class="demo-modal-alert-button" @click="showConfirm">Confirm</button>
  </div>
</template>

<script>
import ModalAlert from 'components/modal/alert.vue'

export default {
  name: 'demo-modal-alert',
  components: {
    ModalAlert
  },
  data () {
    return {
      show1: false,
      show2: false
    }
  },
  methods: {
    showAlert () {
      this.show1 = true
    },
    showConfirm () {
      this.show2 = true
    },
    callClickAlert (bool) {
      this.show1 = false
      console.log(bool)
    },
    callClickConfirm (bool) {
      this.show2 = false
      console.log(bool)
    }
  }
}
</script>

<style lang="pcss">
@namespace demo {
  @b modal-alert {
    @e button {
      font-size: 28px;
      color: #fff;
      width: 100%;
      padding: 20px 0;
      margin-bottom: 1px;
      background-color: #65b1db;
    }
  }
}
</style>



